.hero {
  background: #fff url(../data/hero-bg.svg) 100% 0/ auto no-repeat;
  display: flex;
  justify-content: center;
  padding: 184px 0 300px;
  @include response(991px) {
    padding: 144px 0 260px;
  }
  @include response(767px) {
    background: #fff url(../data/hero-bg.svg) 100% 0/ cover no-repeat;
    padding: 75px 0;
    margin-bottom: 35px;
  }
  @include response(568px) {
    padding: 35px 0;
    margin: 0;
    background: none;
  }
  &__desc {
    max-width: 568px;
    width: 100%;
    margin-right: auto;
    @include response(991px) {
      max-width: 415px;
    }
    @include response(767px) {
      max-width: 280px;
    }
    @include response(568px){
      max-width: 90%;
      margin: 0 auto;
    }
  }
  &__title {
    margin: 0;
    font-size: 28px;
    line-height: 42px;
    font-weight: 800;
    letter-spacing: -0.2px;
    color: $mariner;
    margin: 0 0 55px;
    @include response(991px) {
      margin: 0 0 35px;
    }
    @include response(767px) {
      font-size: 20px;
      line-height: 28px;
    }
    > span {
      font-weight: 400;
    }
  }
  .btn {
    flex: 1 1 253px;
    max-width: 253px;
  }
}

.our-mission {
  margin-bottom: 123px;
  @include response(991px) {
    margin-bottom: 75px;
  }
  @include response(767px) {
    margin-bottom: 45px;
  }
  @include response(479px) {
    margin-bottom: 25px;
  }
  &__container {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-direction: row;
    flex-wrap: wrap;
  }
  &__pic {
    margin-right: 110px;
    @include response(991px) {
      margin-right: 75px;
      padding: 15px 0;
      max-width: 75%;
    }
    @include response(767px) {
      margin: 0;
    }
    @include response(479px) {
      max-width: 100%;
    }
  }
  &__desc {
    flex: 1 1 406px;
    max-width: 406px;
    width: 100%;
    @include response(991px) {
      padding: 15px 0;
      flex: 1 1 75%;
      max-width: 75%;
    }
    @include response(479px) {
      flex: 1 1 auto;
      max-width: 100%;
      text-align: center;
    }
    > .title {
      margin: 0 0 30px;
      text-align: left;
      @include response(991px) {
        margin: 0 0 15px;
      }
      @include response(479px){
        text-align: center;
      }
    }
    .desc {
      > p {
        margin: 0 0 20px;
        @include response(991px) {
          margin: 0 0 15px;
        }
        & + p {
          margin: 0;
        }
      }
    }
  }
}

.trust-triggers {
  margin-bottom: 70px;
  @include response(991px) {
    margin-bottom: 48px;
  }
  @include response(767px) {
    margin-bottom: 34px;
  }
  @include response(479px) {
    margin-bottom: 28px;
  }
  .title {
    text-align: center;
    margin-bottom: 62px;
    @include response(991px) {
      margin-bottom: 42px;
    }
    @include response(767px) {
      margin-bottom: 36px;
    }
    @include response(479px) {
      margin-bottom: 28px;
    }
  }
  &__list {
    max-width: 830px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: center;
    padding: 0;
    list-style: none;
  }
  &__items {
    flex: 1 1 50%;
    margin-bottom: 44px;
    padding-right: 10px;
    @include response(479px) {
      flex: 1 1 auto;
      padding-right: 0;
      margin-bottom: 28px;
    }
  }
  &__inner {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    @include response(479px) {
      justify-content: center;
      flex-direction: column;
      align-items: center;
    }
  }
  &__icon {
    display: flex;
    justify-content: center;
    flex: 0 0  50px;
    min-width: 50px;
    margin-right: 24px;
    @include response(479px){
