      text-align: center;
    }
  }
}

.our-services {
  margin-bottom: 85px;
  @include response(991px) {
    margin-bottom: 48px;
  }
  @include response(767px) {
    margin-bottom: 34px;
  }
  @include response(479px) {
    margin-bottom: 28px;
  }
  .title {
    text-align: center;
    margin-bottom: 10px;
  }
  .subtitle {
    font-size: 16px;
    line-height: 34px;
    text-align: center;
    letter-spacing: -0.2px;
    color: $black;
    margin-bottom: 50px;
    @include response(767px) {
      line-height: 24px;
      margin-bottom: 42px;
    }
    @include response(479px) {
      margin-bottom: 32px;
    }
  }
  &__list {
    display: flex;
    align-items: stretch;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -15px;
    @include response(767px) {
      margin: 0;
    }
    @include response(479px) {
      flex-direction: column;
    }
  }
  &__items {
    padding: 20px 24px 32px;
    box-shadow: 0px 10px 25px #ecf0f7;
    border-radius: 8px;
    background-color: $white;
    flex: 1 1 255px;
    max-width: 255px;
    width: 100%;
    margin: 0 15px 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    @include response(767px) {
      flex: 1 1 calc(50% - 30px);
      max-width: calc(50% - 30px);
      padding: 15px 15px 22px;
    }
    @include response(479px) {
      flex: 1 1 auto;
      max-width: 100%;
      margin: 0 0 15px;
    }
    @include hover() {
      .our-services__icon {
        transform: scale(1.1);
      }
    }
  }
  &__icon {
    display: block;
    margin-bottom: 10px;
    text-align: center;
    height: 57px;
    transform: scale(0.9);
    transition: transform .3s ease;
  }
  &__title {
    font-size: 16px;
    line-height: 20px;
    font-weight: 800;
    text-align: center;
    letter-spacing: -0.2px;
    margin-bottom: 12px;
    display: block;
  }
  &__price {
    font-size: 14px;
    line-height: 26px;
    letter-spacing: -0.2px;
    color: $black;
    margin-bottom: 10px;
    > strong {
      color: $mariner;
      font-size: 16px;
    }
  }
  .list {
    margin-bottom: 35px;
    @include response(479px) {
      margin-bottom: 15px;
    }
    &__items {
      font-size: 14px;
      line-height: 28px;
      letter-spacing: -0.2px;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      padding-left: 18px;
      &::before {
        content: '';
        display: block;
        background: url(../data/check-mark.svg) center / contain no-repeat;
        width: 11px;
        height: 10px;
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
      }
    }
  }
  .btn {
    max-width: 175px;
    padding: 10px 8px;
    font-size: 14px;
    line-height: 18px;
    @include response(479px) {
      max-width: 100%;
    }
  }
}

.our-case {
  @include response(767px){
    margin-bottom: 55px;
  }
  .our-mission__container {
    flex-direction: row-reverse;
