}

.fill-form {
  background-color: rgba(243, 247, 255, 0.75);
  padding: 40px 0 60px;
  margin-bottom: 110px;
  @include response(991px) {
    margin-bottom: 48px;
  }
  @include response(767px) {
    margin-bottom: 34px;
    padding: 40px 0;
  }
  @include response(479px) {
    margin-bottom: 28px;
  }
  .container {
    max-width: 920px;
  }
  .title {
    margin-bottom: 25px;
  }
  &__list {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    margin-bottom: 25px;
    @include response(479px) {
      flex-direction: column;
    }
  }
  &__items {
    font-size: 14px;
    line-height: 22px;
    letter-spacing: -0.2px;
    color: $black;
    position: relative;
    padding-left: 20px;
    max-width: 200px;
    width: 100%;
    margin-right: 60px;
    @include response(991px) {
      margin-right: 30px;
    }
    @include response(767px) {
      margin: 0 15px 15px 0;
      max-width: 100%;
    }
    @include response(479px) {
      max-width: 100%;
      margin: 0 0 15px 0;
    }
    &:nth-child(2) {
      max-width: 250px;
      @include response(479px) {
        max-width: 100%;
      }
    }
    &::before {
      content: '';
      display: block;
      background: url(../data/check-mark.svg) center / contain no-repeat;
      width: 11px;
      height: 10px;
      position: absolute;
      top: 10%;
      left: 0;
    }
  }
  &__hidden {
    display: none;
    @include response(991px) {
      display: flex;
      width: 85%;
      margin-left: auto;
    }
    @include response(767px) {
      width: 100%;
    }
    > img {
      @include response(991px) {
        display: block;
        margin-left: auto;
      }
    }  
  }
  &--bg {
    background: $white url(../data/fill-form-bg.svg) 80% 100% / auto no-repeat;
    padding: 0 0 160px;
    margin: 0;
    @include response(991px) {
      background: none;
      padding: 0 0 74px;
    }
    @include response(767px) {
      padding: 0 0 44px;
    }
    @include response(479px) {
      padding: 0 0 24px;
      
    }
    .fill-form__list {
      margin-bottom: 70px;
      @include response(991px) {
        margin-bottom: 44px;
      }
      @include response(767px) {
        margin-bottom: 24px;
      }
    }
    .form {
      max-width: 445px;
      padding: 44px 10px 50px;
      @include response(991px) {
        margin: 0 auto;
        justify-content: center;
        padding: 42px 15px 28px;
      }
      @include response(767px) {
        max-width: 100%;
      }
      @include response(479px) {
        padding: 36px 15px 28px;
      }
      &__row {
        max-width: 365px;
        margin: 0 0 15px;
        @include response(767px) {
          max-width: 100%;
        }
        & + .form__row {
          margin: 0 0 25px;
        }
      }
      .btn-default {
        @include response(991px) {
          margin: 0;
        }
      } 
    }
  }
}

