.carousel {
  &__items {}
  &__holder {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    margin: 0 auto;
    background: $white;
    border-radius: 8px;
    padding: 75px 60px;
    @include response(1023px){
      padding: 14px;
      flex-direction: column;
      align-items: center;
    }
  }
  &__block {
    margin-right: 110px;
    @include response(1023px){
      margin: 0 0 26px;
    }
    .title {
      font-weight: 800;
      font-size: 16px;
      line-height: 26px;
      letter-spacing: -0.2px;
      margin-bottom: 5px;
    }
    .subtitle {
      font-size: 14px;
      line-height: 20px;
      letter-spacing: -0.2px;
      color: $black;
      margin-bottom: 25px;
    }
  }
  &__pic {
    position: relative;
    &-mac {
      > img {
        width: 100%;
      }
    }
    &-iphone {
      position: absolute;
      top: 95px;
      left: 0;
      left: 0%;
      width: 95px;
      height: 190px;
      > img {
        height: 100%;
      }
    }
  }
}

.carousel-list {
  padding-left: 58px;
  &__items {
    margin-bottom: 20px;
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    p {
      font-weight: 600;
      font-size: 14px;
      line-height: 21px;
      letter-spacing: -0.2px;
      color: $black;
      max-width: 210px;
      width: 100%;
      margin: 0 0 5px;
      span {
        font-weight: 400;
        color: $black;
      }
    }
    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 38px;
      height: 1px;
      background-color: $blue;
      top: 8px;
      left: -58px;
    }
    & + .carousel-list__items {
      margin: 0;
    }
  }
  &__row {
  }
}

.work-stages {
  margin-bottom: 110px;
  @include response(1200px) {
    margin-bottom: 74px;
  }
  @include response(767px) {
    margin-bottom: 44px;
  }
  @include response(479px) {
    margin-bottom: 28px;
  }
  .container {
    max-width: 990px;
  }
  .title {
    text-align: center;
    margin-bottom: 68px;
  }
  &__holder {
    padding-left: 85px;
    @include response(767px) {
      display: flex;
    }
    @include response(668px) {
      padding-left: 44px;
    }
    @include response(479px) {
      flex-direction: column;
    }
  }
  &__row {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    margin-bottom: 80px;
    @include response(479px) {
      margin: 0;
    }
    & + .work-stages__row {
      margin-bottom: 0;
    }
    &:first-of-type {
      .work-stages__inner {
        &::after {
          @include response(479px) {
            height: 90%;
          }
        }
      }
    }
  }
  &__line {
    width: 80%;
    height: 2px;
    background-color: #bdcaf4;
    @include response(767px) {
      display: none;
    }
  }
  &__inner {
    display: flex;
    margin-top: -20px;
    @include response(767px) {
      flex-direction: column;
      position: relative;
    }
    @include response(479px) {
      width: 100%;
    }
    &::after {
      content: '';
      display: block;
      @include response(767px) {
        height: 65%;
        width: 2px;
        background-color: #bdcaf4;
        position: absolute;
        top: 40px;
        left: 20px;
        z-index: 3;
      }
    }
    &:first-of-type,
    &:last-of-type {
      .work-stages__step {
        &:last-child {
          padding-right: 0;
        }
      }
    }
  }
  &__step {
    padding-right: 138px;
    @include response(767px) {
      display: flex;
      padding-right: 48px;
      margin-bottom: 45px;
    }
    @include response(668px) {
      padding-right: 16px;
    }
  }
  &__desc {
    @include response(767px) {
      padding-left: 55px;
    }
  }
  &__days {
    display: block;
    font-weight: 600;
    font-size: 16px;
    line-height: 34px;
    letter-spacing: -0.2px;
    margin-bottom: 3px;
  }
  &__txt {
    margin: 0;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: -0.2px;
    color: $black;
    max-width: 205px;
    width: 100%;
  }
}

.step {
  margin-bottom: 15px;
  display: inline-block;
  background: transparent;
  transition: all 0.5s ease;
  position: relative;
  background: $white;
  @include response(767px) {
    position: absolute;
    z-index: 5;
    top: unset;
    left: unset;
  }
  &__count {
    font-style: normal;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    border: 2px solid #bdcaf4;
    width: 40px;
    height: 40px;
    font-weight: 800;
    font-size: 18px;
    line-height: 32px;
    color: #bdcaf4
  }
  &.is-active {
    &::after {
      content: '';
      display: block;
      height: 55px;
      width: 55px;
      background: url(../data/active-bg.svg) center / contain no-repeat;
      position: absolute;
      top: -10px;
      left: -10px;
    }
    .step__count {
      border: 2px solid #4a6ee0;
      color: #4c71dd;
    }
  }
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  display: none;
  &__overlay {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  &__holder {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__close {
    position: absolute;
    right: 15px;
    top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: transparent;
    border: none;
    outline: none;
    transform: rotate(45deg);
    width: 28px;
    height: 28px;
    padding: 15px;
    cursor: pointer;
    transition: 0.3s linear;
    @include hover() {
      &::after,
      &::before {
        background-color: $blue;
      }
    }
    &::after,
    &::before {
      content: '';
      display: block;
      position: absolute;
      background-color: $black;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: 0.3s ease;
    }
    &::after{
      height: 2px;
      width: 27px;
      @include response(479px){
        width: 20px;
      }      
    }
    &::before {
      width: 2px;
      height: 27px;
      @include response(479px){
        height: 20px;
      }
    }
  }
  &__content {
    position: relative;
    .form {
      flex-direction: column;
      box-shadow: none;
      padding: 55px 42px;
      @include response(479px){
        padding: 55px 15px 45px;
      }
      &__title {
        font-weight: 800;
        font-size: 22px;
        line-height: 30px;
        text-align: center;
        letter-spacing: -0.175px;
        color: #333;
        max-width: 410px;
        margin: 0 0 25px;
        @include response(479px){
          font-size: 18px;
          line-height: 24px;
        }
      }
      &__row {
        flex: 1 1 auto;
        max-width: 365px;
        margin: 0 0 15px;
        @include response(767px) {
          max-width: 100%;
        }
      }
      &__notific {
        max-width: 380px;
        width: 100%;
        margin: 0 auto;
        font-size: 14px;
        line-height: 22px;
        text-align: center;
        letter-spacing: -0.2px;
        color: #333;
      }
      .btn-default {
        flex: 1 1 auto;
        margin-bottom: 35px;
        @include response(479px){
          margin-bottom: 18px;
        }
      }
    }
  }
  &.open {
    display: block;
  }
}