.btn {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 600;
  letter-spacing: -0.2px;
  color: $white;
  text-decoration: none;
  background-color: $blue;
  border-radius: 30px;
  border: 1px solid transparent;
  box-shadow: 0px 10px 16px #e3e8f3;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  padding: 15px 8px;
  cursor: pointer;
  width: 100%;
  transition: all .3s cubic-bezier(0.4, 0, 1, 1);
  @include response(767px) {
    padding: 8px;
  }
}
.btn-default {
  @include hover() {
    background-color: #477cec;
    box-shadow: 0px 10px 25px #d6e0f1;
  }
  &:active {
    box-shadow: none;
    background-color: $blue;
  }
}
.btn-disabled {
  pointer-events: none;
  background-color: $gray;
}