.breadcrumbs {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 15px;
  &__list {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
  }
  &__items {
    margin: 0 32px 8px 0;
  }
  &__link {
    color: green;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: relative;
    transition: .2s ease-out;
    font-size: 15px;
    line-height: 22px;
    border-bottom: 1px dashed green;
    opacity: 0.8;
    @include hover() {
      opacity: 1;
      border-bottom: 1px dashed transparent;
    }
    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 3px;
      height: 3px;
      border-radius: 50%;
      background: $black;
      right: 0;
      top: 53%;
      transform: translate(20px, -50%);
    }
  }
  &__current {
    color: $black;
    font-size: 15px;
    line-height: 22px;
  }
}