.footer {
  height: 90px;
  box-shadow: 0px -2px 2px #ecedf3;
  @include response(767px) {
    height: auto;
    padding: 22px 15px;
  }
  &__container {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include response(767px) {
      flex-direction: column-reverse;
    }
    .logo {
      margin: 0;
      flex: 1 1 auto;
      @include response(991px){
        max-width: unset;
        text-align: center;
      }
    }
  }
}
.privacy-policy {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: $black;
  transition: color .3s linear;
  flex: 1 1 auto;
  text-align: center;
  @include response(991px){
    flex: none;
  }
  @include response(767px) {
    margin-bottom: 16px;
  }
  @include hover() {
    color: $blue;
  }
}

.soc {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 -15px;
  flex: 1 1 auto;
  justify-content: flex-end;
  @include response(991px){
    flex: none;
  }
  @include response(767px) {
    margin: 0 0 16px;
  }
  &__list {
  }
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 15px;
    @include response(767px) {
      padding: 0 10px;
    }
    @include hover() {
      > svg {
        > path {
          fill: none;
          animation: svg 1s linear forwards;
        }
      }
    }
    >svg {
      > path {
        animation: none 1s linear forwards;
        transition: all 0.3s ease-in;
      }
    }
  }
}

.vk {
  stroke-dasharray: 95;
  stroke-dashoffset: 95;
}

.fb {
  stroke-dasharray: 78;
  stroke-dashoffset: 78;
}

@keyframes svg {
  to {
    stroke-dashoffset: 0;
  }
}
