body {
  height: 100%;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  color: $black;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: -0.2px;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: none;
}
* {
  outline: none;
  box-sizing: border-box;
  &::after,
  &::before {
    box-sizing: border-box;
  }
}
svg {
  transform: translateZ(0);
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
input[type="submit"] {
  cursor: pointer;
}
h1 {
  font-size: 34px;
  line-height: 36px;
  font-weight: 700;
}
h2 {
  font-size: 24px;
  line-height: 26px;
  font-weight: normal;
}
h3 {
  font-size: 22px;
  line-height: 24px;
  font-weight: normal;
}
h4 {
  font-size: 16px;
  line-height: 18px;
  font-weight: normal;
}
h5 {
  font-size: 12px;
  line-height: 14px;
  font-weight: normal;
}
h6 {
  font-size: 10px;
  line-height: 12px;
  font-weight: normal;
}
p.error {
  font-size: 12px;
  line-height: 14px;
  color: red;
}
.wrapper {
  overflow: hidden;
  min-height: 100vh;
  width: 100%;
  position: relative;
  padding-top: 70px;
}
.holder-block {
  overflow: hidden;
  position: relative;
  height: 100%;
}
.no-scroll {
  overflow: hidden;
  position: relative;
  height: 100%;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  @include response(767px) {
    padding: 0 10px;
  }
}

// Form styles 
.form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-color: $white;
  box-shadow: 0px 10px 25px #ecf0f7;
  border-radius: 8px;
  max-width: 920px;
  width: 100%;
  padding: 30px 10px 22px;
  @include response(991px) {
    justify-content: flex-start;
  }
  @include response(767px) {
    flex-direction: column;
  }
  &__row {
    flex: 1 0 290px;
    max-width: 290px;
    width: 100%;
    margin: 0 25px 10px 0;
    @include response(767px) {
      flex: 1 1 auto;
      max-width: 100%;
      margin: 0 0 25px;
    }
    @include response(479px) {
      margin: 0 0 15px;
    }
  }
  &__control{
    font-family: 'Montserrat', sans-serif;
    display: inline-block;
    vertical-align: top;
    width: 100%;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: -0.2px;
    color: #000000;
    height: 44px;
    font-weight: 400;
    padding: 14px 20px;
    background-color: #f3f7ff;
    border: 1px solid transparent;
    border-radius: 30px;
    outline: none;
    resize: none;
    margin: 0;
    transition: all .2s;
    @include response(479px){
      height: 40px;
      padding: 8px 18px;
    }
    &:focus {
      border: 1px solid $mariner;
    }
  }
  *::-webkit-input-placeholder {
    opacity: 0.35; 
    color: $black;
  }
  *:-moz-placeholder {
    opacity: 0.35; 
    color: $black;
  }
  *::-moz-placeholder {
    opacity: 0.35; 
    color: $black;
  }
  *:-ms-input-placeholder {
    opacity: 0.35; 
    color: $black;
  }
  .btn-default {
    flex: 1 0 190px;
    max-width: 190px;
    padding: 11px 8px;
    font-weight: 600;
    font-size: 14px;
    line-height: 18px;
    text-align: center;
    margin-bottom: 10px;
    @include response(767px) {
      flex: 1 1 auto;
      max-width: 100%;
      margin: 0 0 15px;
    }
  }
}

.slick-dots {
  position: absolute;
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
  > li {
    height: 7px;
    width: 20px;
    border-radius: 25px;
    color: transparent;
    background: #edf3ff;
    cursor: pointer;
    position: relative;
    margin: 0 5px;
    > button {
      height: 100%;
      width: 100%;
      border: none;
      background: transparent;
      color: transparent;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      cursor: pointer;
    }
    &.slick-active {
      background-color: #3d66dd;
    }
  }
}

.title {
  font-size: 28px;
  line-height: 34px;
  font-weight: 600;
  letter-spacing: -0.2px;
  color: $black;
  @include response(991px) {
    font-size: 24px;
    line-height: 30px;
  }
  @include response(767px) {
    font-size: 22px;
    line-height: 28px;
  }
  @include response(479px) {
    font-size: 20px;
    line-height: 26px;
    text-align: center;
  }
}


.logo-anim {
  fill: #2E58D1;
  // fill: none;
  stroke-dasharray: 108;
  stroke-dashoffset: 108;
  animation: logo1 4s linear 1;
  @include hover() {
    animation: logo 3s linear 1 forwards;
    fill: none;
  }
}


@keyframes logo {
  to {
    stroke-dashoffset: 0;
  }
}