.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 999;
  height: 70px;
  box-shadow: 0px 2px 2px #ecedf3;
  background-color: $white;
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
  }
}

.logo {
  margin-right: auto;
  flex: 1 0 auto;
  transition: all 0.2s ease-in;
  @include hover() {
    svg {
      .logo-anim {
        fill: #2E58D1;
        stroke-dasharray: 108;
        stroke-dashoffset: 108;
        animation: logo 2s linear 1 forwards;
        fill: none;
      }
    }
  }
  @include response(991px) {
    max-width: 175px;
    width: 100%;
  }
  > svg {
    width: 100%;
  }
  &.animate {
    svg {
      .logo-anim {
        fill: none;
        animation: logo 2s linear 1 forwards;
      }
    }
  }
}

.nav {
  width: 100%;
  height: 100%;
  @include response(1023px) {
    height: 100vh;
    width: 70%;
    overflow-y: auto;
    margin-left: auto;
    padding: 45px 15px;
    background: #fff;
  }
  @include response(479px) {
    width: 100%;
  }
  ul {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0 -13px;
    height: 100%;
    @include response(1023px) {
      flex-direction: column;
      height: auto;
      width: 100%;
      margin: 0;
    }
    li {
      padding: 0 13px;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      @include response(1023px) {
        width: 100%;
        padding: 0;
      }
      a {
        font-size: 14px;
        line-height: 18px;
        font-weight: 600;
        letter-spacing: -0.2px;
        color: $gray;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 12px;
        position: relative;
        text-transform: uppercase;
        transition: color .3s cubic-bezier(0.42, 0, 0.05, 0.97);
        @include response(1023px) {
          width: 100%;
          padding: 0 10px 25px;
        }
        @include hover() {
          color: $black;
          &::after {
            transform: translate(0, 1px) scaleX(1);
            width: 100%;
          }
        }
        &::after {
          content: '';
          display: block;
          width: 0;
          height: 4px;
          background-color: $mariner;
          position: absolute;
          bottom: -2px;
          left: 0;
          right: 0;
          transition: transform .3s cubic-bezier(0.54, 0.23, 0.62, 0.89);
          transform: translate(-50%, 0) scaleX(0);
        }
      }
    }
  }
}

.mobile-menu {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-right: 78px;
  height: 100%;
  position: relative;
  @include response(1200px) {
    margin-right: 48px;
  }
  @include response(1023px) {
    position: fixed;
    top: 70px;
    right: 0;
    height: calc(100vh - 70px);
    background-color: transparent;
    margin: 0;
    flex-direction: column;
    justify-content: space-between;
    overflow-y: auto;
    width: 100%;
    transform: translateX(100%);
    transition: transform 0.5s ease-in, background-color 0.8s ease-in;
  }
  &.is-active {
    transform: translateX(0);
    background-color: rgba(0, 0, 0, 0.7);
  }
}

.header-contact {
  display: flex;
  align-items: center;
  justify-content: center;
  @include response(1023px) {
    margin-right: 35px;
    width: 100%;
    justify-content: flex-end;
  }
  @include response(767px){
    margin-right: 18px;
  }
  @include response(567px) {
    margin: 0;
    flex-direction: column;
  }
  &__link {
    font-size: 14px;
    line-height: 18px;
    font-weight: 600;
    letter-spacing: -0.2px;
    flex: 1 0 auto;
    padding: 12px 0;
    color: $black;
    margin-right: 28px;
    transition: color .3s cubic-bezier(0.42, 0, 0.05, 0.97);
    @include hover() {
      color: $blue;
    }
    @include response(1200px) {
      margin-right: 20px;
    }
    @include response(1023px) {
      flex: none;
    }
    @include response(567px) {
      margin: 0;
      width: 100%;
      text-align: center;
      margin-bottom: 14px;
    }
  }
  .btn-default {
    font-size: 14px;
    line-height: 16px;
    letter-spacing: -0.2px;
    flex: 1 0 170px;
    max-width: 170px;
    box-shadow: none;
    @include response(567px) {
      flex: 1 1 auto;
    }
    @include hover() {
      box-shadow: 0px 10px 25px #d6e0f1;
    }
    &:active {
      box-shadow: none;
      background-color: $blue;
    }
    
  }
}

.burger {
  display: none;
  @include response(1023px) {
    display: block;
  }
  &__holder {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-end;
    height: 35px;
    width: 35px;
    padding: 5px;
    cursor: pointer;
    &.active {
      span {
        // background-color: #195deb;
        &::after {
          background-color:#195deb;
          background-color: $black;
          background-color: $blue;
          transform: translateX(0);
        }
        &:nth-of-type(2),
        &:last-of-type {
          width: 100%;
        }
      }
    }
  }
  span {
    width: 100%;
    height: 2px;
    background-color: $blue;
    background-color: $black;
    position: relative;
    display: block;
    transition: .3s ease-in-out;
    overflow: hidden;
    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translateX(100%);
      width: 100%;
      height: 100%;
      transition: .7s ease-in-out;
    }
    &:first-of-type {
      width: 100%;
    }
    &:nth-of-type(2) {
      width: 75%;
    }
    &:last-of-type {
      width: 55%;
    }
  }
}
